import React, { useState, useEffect, useRef } from 'react';
import './App.css';

import MarketComponent from './components/MarketComponent';
import SettingComponent from './components/SettingComponent';
import ChartComponent from './components/ChartComponent';

function App() {

  const [page, setPage] = useState('market');
  const [marketList, setMarketList] = useState(["KRW-BTC"]);
  const [loading, setLoading] = useState(true);

  const __setPage = (p) => {
    setPage(p)
  }

  useEffect(() => {
    console.log('app');
    if(localStorage.getItem('market_list')) {
      setMarketList(JSON.parse(localStorage.getItem('market_list')));
    }
    setLoading(false);
  }, []);

  if(loading) {
    return (
<div className={"container"}>Loading...</div>
    );
  } else {
    return (
      <div className={"container"}>
        
        <div className={"menu-bar"}>
          <div
            className={"menu-button " + (page === "market" ? "active" : "")}
            onClick={() => __setPage('market')}
          >
            {"[ Market ]"}
          </div>
          <div
            className={"menu-button " + (page === "setting" ? "active" : "")}
            onClick={() => __setPage('setting')}
          >
            {"[ Setting ]"}
          </div>
        </div>
  
        {page === "market" ? (
          <MarketComponent marketList={marketList} />
        ) : (
          <SettingComponent marketList={marketList} setMarketList={setMarketList} />
        )}
        
  
      </div>
    );
  }
}

export default App;
