import React, { useState, useEffect, useRef } from 'react';
import { Scrollbars } from 'react-custom-scrollbars';
import axios from 'axios';

function SettingComponent({ marketList, setMarketList }) {

  const [marketAll, setMarketAll] = useState([]);
  // const [checkedMarket, setCheckedMarket] = useState([]);

  const __checkedMarket = (checked, id) => {
    if (checked) {
      setMarketList([...marketList, id]);
      localStorage.setItem('market_list', JSON.stringify([...marketList, id]));
    } else {
      setMarketList(marketList.filter((el) => el !== id));
      localStorage.setItem('market_list', JSON.stringify(marketList.filter((el) => el !== id)));
    }
  }

  useEffect(() => {
    console.log('setting component');

    axios.get('https://api.upbit.com/v1/market/all').then(function (response) {
      setMarketAll(response.data?.filter(item => 
        item.market.indexOf('KRW-') !== -1
      ));
    });

  }, []);

  return (

    <Scrollbars>
      <div className={"theme-container"}>

        <table style={{paddingBottom: 10}}>
          <thead>
            <tr>
              <th></th>
              <th>Market</th>
              <th>Name</th>
            </tr>
          </thead>
          <tbody>

            {marketAll?.map(item => (

              <tr key={item.code}>

                <td>
                  <input
                    id={item.market}
                    type="checkbox"
                    name="checkboxMarket"
                    onChange={(e) => {
                      __checkedMarket(e.currentTarget.checked, item.market)
                    }}
                    checked={marketList.includes(item.market) ? true : false}
                  />
                  <label for={item.market}>
                    <p>{""}</p>
                  </label>
                </td>
                <td><label htmlFor={item.market}>{item.market}</label></td>
                <td><label htmlFor={item.market}>{item.korean_name}</label></td>
              </tr>
            ))}
          </tbody>
        </table>

      </div>
    </Scrollbars>

  );
}

export default SettingComponent;
