import React, { useState, useEffect, useRef } from 'react';
import { Scrollbars } from 'react-custom-scrollbars';
import ApexChart from 'react-apexcharts';
import axios from 'axios';

function ChartComponent({chartMarket}) {

  // const [marketAll, setMarketAll] = useState([]);
  const [candleData, setCandleData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {

    console.log('chart component');
    axios.get('https://api.upbit.com/v1/candles/minutes/5?market=' + chartMarket +'&count=20').then(function (response) {
      setCandleData(response.data?.map((item) => {
        return [
          Date.parse(item.candle_date_time_utc),
          item.opening_price,
          item.high_price,
          item.low_price,
          item.trade_price,
        ];
      }));
      setLoading(false);
    })

  }, [chartMarket]);

  if (loading) {
    return (
      <div>loading...</div>
    )

  } else {
    return (
      <ApexChart
        type="candlestick"
        options={{
          chart: {
            type: 'candlestick',
            offsetX: 0,
            offsetY: 0,
            zoom: {
              enabled: false
            },
            toolbar: {
              show:false,
            },
            background: "transparent",
          },
          xaxis: {
            labels:{
              show: false
            },
            axisBorder: {
              show: false,
            },
            axisTicks: {
              show: false,
            },
            tooltip: {
              enabled: true
            }
          },
          yaxis: {
            labels:{
              show: false,
            },
            tooltip: {
              enabled: true
            }
          },
          grid: {
            show: false,
            padding: {
              left: -20,
              right: 0,
              top: -20,
              bottom: -20
            }
          },
          tooltip: {
            enabled: false
          },
          legend: {
            offsetX: 0,
            offsetY: 0
          },
          plotOptions: {
            candlestick: {
              colors: {
                upward: '#bfbfbf',
                downward: '#434343'
              }
            }
          }
        }}
        series={[
          {
            data: candleData
          }
        ]}
        height={150}
      />
    );
  }
}

export default ChartComponent;
