import React, { useState, useEffect, useRef } from 'react';
import { Scrollbars } from 'react-custom-scrollbars';
import ChartComponent from './ChartComponent';

function MarketComponent({ marketList }) {

  const webSocketUrl = 'wss://api.upbit.com/websocket/v1';
  const ws = useRef(null);

  const [socketConnected, setSocketConnected] = useState(false);
  const [marketData, setMarketData] = useState(null);
  const [chartMarket, setChartMarket] = useState(marketList[0]);

  const __chartMarket = (cm) => {
    setChartMarket(cm);
  }

  useEffect(() => {

    console.log('market component');

    if (marketList) {
      var marketDataTemp = [];
      for (var i = 0; i < marketList.length; i++) {
        marketDataTemp.push({ "code": marketList[i] });
      }
      setMarketData(marketDataTemp);
    }

    if (!ws.current) {
      ws.current = new WebSocket(webSocketUrl);
      ws.current.binaryType = 'arraybuffer';
      ws.current.onopen = () => {
        console.log("connected to " + webSocketUrl);
        var msg = [
          {
            "ticket": "TEST",
          },
          {
            "type": "ticker",
            "codes": marketList
          }
        ];
        ws.current.send(JSON.stringify(msg));
        setSocketConnected(true);
      };
      ws.current.onclose = (error) => {
        console.log("disconnect from " + webSocketUrl);
        setSocketConnected(false);
      };
      ws.current.onerror = (error) => {
        console.log("connection error " + webSocketUrl);
        console.log(error);
      };
      ws.current.onmessage = (evt) => {

        const enc = new TextDecoder("utf-8");
        const arr = new Uint8Array(evt.data);
        const data = JSON.parse(enc.decode(arr));

        setMarketData(prevState => {
          var tem = new Array(prevState.length);;
          for (var i = 0; i < prevState.length; i++) {
            if (prevState[i].code === data.code) {
              tem[i] = data;
            } else {
              tem[i] = prevState[i];
            }
          }
          return tem;
        });

      };
    }
    return () => {
      console.log("clean up");
      ws.current.close();
    };

  }, []);

  return (
    <Scrollbars>
      <div className={"theme-container"}>
        <table style={{ paddingBottom: 10 }}>
          <thead>
            <tr>
              <th colSpan={4}>
                <ChartComponent chartMarket={chartMarket} />
              </th>
            </tr>
            <tr>
              <th>Name</th>
              <th>Price</th>
              <th>Change</th>
              <th>Volume</th>
            </tr>
          </thead>
          <tbody>

            {marketData?.map(item => (
              <tr key={item.code} onClick={() => __chartMarket(item.code)}>
                <td>{item.code}</td>
                <td>
                  {item.trade_price?.toLocaleString(undefined, {
                    minimumFractionDigits: 0,
                    maximumFractionDigits: 4
                  })}
                </td>
                <td>
                  {(item.signed_change_rate * 100)?.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2
                  }) + "%"}
                </td>
                <td>
                  {(item.acc_trade_price_24h / 1000000)?.toLocaleString(undefined, {
                    minimumFractionDigits: 0,
                    maximumFractionDigits: 0
                  }) + "M"}
                </td>
              </tr>
            ))}

          </tbody>
        </table>
      </div>
    </Scrollbars>

  );
}

export default MarketComponent;
